// root/frontend/tests/unit/screens/ReviewScreen.unit.test.tsx
import React from 'react';
import { render } from '@testing-library/react-native';
import ReviewScreen from '../../../src/screens/ReviewScreen';

// react-native-paper 모킹
jest.mock('react-native-paper', () => {
  return {
    SegmentedButtons: () => null,
    Card: () => null,
    Button: () => null,
    Title: () => null,
    Paragraph: () => null,
    useTheme: () => ({
      colors: {
        primary: '#000',
        background: '#fff',
      }
    })
  };
});

// 카드 하위 컴포넌트 모킹 추가
jest.mock('react-native-paper/lib/commonjs/components/Card/Card', () => {
  return {
    __esModule: true,
    default: () => null,
    Cover: () => null,
    Content: () => null,
  };
});

// 이미지 모킹
jest.mock('react-native', () => {
  const rn = jest.requireActual('react-native');
  rn.Image = () => null;
  return rn;
});

describe('ReviewScreen 단위 테스트', () => {
  it('컴포넌트가 정의되어 있어야 함', () => {
    expect(ReviewScreen).toBeDefined();
  });

  it('컴포넌트가 함수여야 함', () => {
    expect(typeof ReviewScreen).toBe('function');
  });

  it('오류 없이 렌더링되어야 함', () => {
    expect(() => {
      render(<ReviewScreen />);
    }).not.toThrow();
  });
});